import React, { useState, useEffect } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link, useLocation, useHistory
} from "react-router-dom";
import routeNavBar from "./RouteNavBar";

type Props = {};

function HomeButton() {
    let history = useHistory();

    function handleClick() {
        history.push("/home");
    }
    return (
        <button type="button" onClick={handleClick}>
            Go home
        </button>
    );
}

function NavBarItems(props: Props) {
    const { pathname } = useLocation();
    //check with ? if there is a title label => direct -- from typescript 4 
    const titlelabel = routeNavBar[pathname]?.title;
    //or : console titlelable = routeNavBar[pathname] ? routeNavBar[pathname].title : "";
    return (
        <div className="page-header">
            <div className="navigation-bar">
                <div className="logo">
                    <img src="/logo/ecolife-logo.jpg" />
                </div>
                <div className="navigation-bar-item">
                    <ul>
                        {Object.keys(routeNavBar).map((route) => {
                            const { title } = routeNavBar[route];
                            return (
                                <li key={`li_${route}`} className="nav-item">
                                    <Link className={`nav-link ${pathname === route ? 'active' : ""}`} to={route}>
                                        {title}
                                    </Link>
                                </li>
                            );
                        })}
                        <li className="w3-xlarge" style={{float:'right'}}><a><i className="fa fa-shopping-cart"></i></a></li>
                        <li className="w3-xlarge" style={{float:'right'}}><a><i className="fa fa-search"/></a></li>
                    </ul>
            
                
    
        
                </div>
            </div>
        </div>
    )
}
export default NavBarItems;